@import '../../common/styles/sass/variables';

.nav {
  //border: 1px solid #ff008a;
  display: flex;
  width: 300px;
  margin: 5px;
  justify-content: flex-end;
  font-family: $fontFamily;

  .active{
    background-color: $backgroundColor;
    box-shadow: inset 0 -30px 0 $textColor, 0 1px 0 $textColor;
    border-radius: 5px;
  }
  .link {
    padding: 5px;
    font-size: 24px;
    text-decoration: none;
    color: $textColor;
    box-shadow: inset 0 -1px 0 $textColor, 0 1px 0 $textColor;
    transition: box-shadow .3s;
    @media screen and (max-width: 530px) {
      font-size: 18px;
    }

    &:hover {
      background-color: $backgroundColor;
      box-shadow: inset 0 -30px 0 $textColor, 0 1px 0 $textColor;
      color: $backgroundColor;
      border-radius: 5px;
      cursor: pointer;
    }

  }
  @media screen and (max-width: 400px) {
    display: none;
  }
}
